import React, { useContext } from 'react'
import { DataContext } from '../../store/contexts/dataContext';
import moment from 'moment'

const UpcomingWork = () => {

  const { items } = useContext(DataContext)

  const getNextMonday = () => {
    const currentCheck = moment().startOf('day').add(1,'d')
    var nextMonday = null;
    while (nextMonday === null){
      if(currentCheck.day() === 1){
        nextMonday = currentCheck
      }else{
        currentCheck.add(1,'d')
      }
    }
    return nextMonday;
  }

  const todaysItems = items ? items.filter(item => {
    return item.daysTo <= 0;
  }) : null;

  const tomorrowsItems = items ? items.filter(item => { 
    return item.daysTo === 1;
  }) : null;

  const thisWeeksItems = items ? items.filter(item => {
    return moment.unix(item.expectedUpdate.seconds).startOf('days').isBefore(getNextMonday())
  }) : null

  const addScores = (itemsToAdd) => {
    if(itemsToAdd === null) return 0
    var score = 0
    itemsToAdd.forEach(item => {
      score += item.score
    })
    return score
  }

  const todaysScore = addScores(todaysItems);
  const tomorrowsScore = addScores(tomorrowsItems)
  const thisWeeksScore = addScores(thisWeeksItems)

  return ( 
    <div className="grid grid-cols-3">
      <div>
        <p className="text-center text-2xl">{todaysScore.toFixed(2)}</p>
        <p className="text-center">Today</p>
      </div>
      <div>
        <p className="text-center text-2xl">{tomorrowsScore.toFixed(2)}</p>
        <p className="text-center">Tomorrow</p>
      </div>
      <div>
        <p className="text-center text-2xl">{thisWeeksScore.toFixed(2)}</p>
        <p className="text-center">Rest of Week</p>
      </div>
    </div>
   );
}
 
export default UpcomingWork;